<template>
  <v-app class="appbackground">
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span>ETH</span>
        <span class="font-weight-light">Lottery Mining</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn flat target="_blank" v-show="false">
        <span class="mr-2">Language</span>
      </v-btn>
    </v-toolbar>

    <v-content class="content">
      <HomePage
        :bettingInfo="bettingInfo"
        :lastLucyNum="lastLucyNum"
        :awardAmt="awardAmt"
        :winner="winner"
        :relink="relink"
      />
    </v-content>
    <Dialog :dialog="showDialog" :dialogMsg="dialogMsg"/>
  </v-app>
</template>

<script>
/* eslint-disable */

import HomePage from "./components/HomePage";
import Dialog from "./components/Dialog";
import utils from "./components/utils";
import { Base64 } from "js-base64";

export default {
  name: "App",
  components: {
    HomePage,
    Dialog
  },
  data() {
    return {
      showDialog: false,
      dialogMsg:
        'Ethlotterymining is running on the Ethereum public blockchain, therefore you\'ll need the <a href="https://metamask.io/" target="_blank">MetaMask</a> browser extension to be able to use it with Chrome or FireFox Explorer，After install MeatMask ,please refresh webpage.',
      lastLucyNum: "--",
      awardAmt: "--",
      winner: "--",
      relink: "--",
      bettingInfo: [],
      lastbettinglength: 0
    };
  },
  mounted() {
    var isChrome = navigator.userAgent.toUpperCase().indexOf("CHROME");
    var isFirefox = navigator.userAgent.toUpperCase().indexOf("FIREFOX");

    if (!isChrome && !isFirefox) {
      this.showDialog = true;
    }

    var _this = this;
    if (typeof web3 !== "undefined") {
      console.log("metamask checked");
      // 使用 Mist/MetaMask 的提供者
      this.myGlobal.web3Provider = new Web3(web3.currentProvider);
      //设置合约变量
      this.myGlobal.contract = new this.myGlobal.web3Provider.eth.Contract(
        this.myGlobal.contractabi,
        this.myGlobal.contractAddress
      );

      //1.0 can use
      //       web3.eth.net.getNetworkType()
      // .then(console.log);
      web3.version.getNetwork((err, netId) => {
        switch (netId) {
          case "1":
            console.log("This is mainnet");
            break;
          case "2":
            _this.showDialog = true;
            _this.dialogMsg =
              "Please Note : you are using the deprecated Morden test network.";
            console.log("This is the deprecated Morden test network.");
            break;
          case "3":
            _this.showDialog = true;
            _this.dialogMsg =
              "Please Note : you are using the ropsten test network.";
            console.log("This is the ropsten test network.");
            break;
          default:
            _this.showDialog = true;
            _this.dialogMsg = "Please Note : you are using an unknown network.";
            console.log("This is an unknown network.");
        }
      });

      //设置当前账号
      this.myGlobal.web3Provider.eth.getAccounts().then(function(accounts) {
        _this.myGlobal.userAccount = accounts[0];

        if (typeof accounts[0] === "undefined") {
          _this.showDialog = true;
          _this.dialogMsg =
            "You may not be logged in to the MetaMask, Please login to use.Ignore this prompt if you have already logged in.";
        }
      });

      //切换账号检测
      var accountInterval = setInterval(function() {
        _this.myGlobal.web3Provider.eth.getAccounts().then(function(accounts) {
          if (_this.myGlobal.userAccount !== accounts[0]) {
            _this.myGlobal.userAccount = accounts[0];
          }

          // var enaddr = utils.encrypt(_this.myGlobal.userAccount);
          // var enb64 = Base64.encode(enaddr);
          _this.relink =
            "www.ethlotterymining.com/" + _this.myGlobal.userAccount;
        });
      }, 1000);

      this.getPrevWinInfo();

      // this.bettingListener();
      this.getpastBettingInfo();
      //定时查询有没有新的
      setInterval(function() {
        _this.checkNewBetting();
      }, 3000);

      this.lotteryEventListener();
    } else {
      this.showDialog = true;
      console.log("metamask is not exist");
    }
  },
  methods: {
    lotteryEventListener() {
      var _this = this;
      // 合约事件监听
      this.myGlobal.contract.events
        .LotteryEvent()
        .on("data", function(data) {
          var returnValue = data.returnValues;

          _this.lastLucyNum = returnValue.lucyNumber;
          _this.awardAmt = web3.fromWei(returnValue.amt);
          _this.winner = returnValue.lucyer;

          if (_this.winner === _this.myGlobal.userAccount) {
            _this.$toasted.show("Congratulations! You Win!", {
              theme: "bubble",
              position: "top-center",
              duration: 3000
            });
          }
        })
        .on("error", console.error);
    },
    getPrevWinInfo() {
      var _this = this;
      this.myGlobal.contract.methods
        .lucyNumber()
        .call()
        .then(function(result) {
          _this.lastLucyNum = result;
        });
      this.myGlobal.contract.methods
        .lastRewardAmt()
        .call()
        .then(function(result) {
          _this.awardAmt = web3.fromWei(result);
        });

      this.myGlobal.contract.methods
        .winner()
        .call()
        .then(function(result) {
          _this.winner = result;
        });
    },
    bettingListener() {
      var _this = this;
      // 合约事件监听
      this.myGlobal.contract.events
        .BettingEvent()
        .on("data", function(data) {
          var returnValue = data.returnValues;

          _this.bettingInfo.unshift({
            number: returnValue.number,
            betaker: returnValue.better,
            time: _this.convertUTCTimeToLocalTime(
              new Date(returnValue.time * 1000)
            ),
            ismy:
              returnValue.better === _this.myGlobal.userAccount ? "true" : false
          });
        })
        .on("error", console.error);
    },
    getpastBettingInfo() {
      var _this = this;
      this.myGlobal.contract
        .getPastEvents("BettingEvent", {
          fromBlock: 6919436,
          toBlock: "latest"
        })
        .then(function(events) {
          _this.lastbettinglength = events.length;
          for (var i = events.length - 1; i >= 0; i--) {
            _this.bettingInfo.push({
              number: events[i].returnValues.number,
              betaker: events[i].returnValues.better,
              time: _this.convertUTCTimeToLocalTime(
                new Date(events[i].returnValues.time * 1000)
              ),
              ismy:
                events[i].returnValues.better === _this.myGlobal.userAccount
                  ? "true"
                  : false
            });
          }
        });
    },
    checkNewBetting() {
      var _this = this;
      this.myGlobal.contract
        .getPastEvents("BettingEvent", {
          fromBlock: 6919436,
          toBlock: "latest"
        })
        .then(function(events) {
          // console.log(events.length)
          // console.log(_this.lastbettinglength)
          if (events.length > _this.lastbettinglength) {
            for (
              var i = events.length - 1;
              i > _this.lastbettinglength - 1;
              i--
            ) {
              _this.bettingInfo.unshift({
                number: events[i].returnValues.number,
                betaker: events[i].returnValues.better,
                time: _this.convertUTCTimeToLocalTime(
                  new Date(events[i].returnValues.time * 1000)
                ),
                ismy:
                  events[i].returnValues.better === _this.myGlobal.userAccount
                    ? "true"
                    : false
              });

              _this.lastbettinglength = events.length;
            }
          }
        });
    },
    convertUTCTimeToLocalTime: function(UTCDateString) {
      if (!UTCDateString) {
        return "-";
      }
      function formatFunc(str) {
        //格式化显示
        return str > 9 ? str : "0" + str;
      }
      var date2 = new Date(UTCDateString); //这步是关键
      var year = date2.getFullYear();
      var mon = formatFunc(date2.getMonth() + 1);
      var day = formatFunc(date2.getDate());
      var hour = date2.getHours();
      var noon = hour >= 12 ? "PM" : "AM";
      hour = hour >= 12 ? hour - 12 : hour;
      hour = formatFunc(hour);
      var min = formatFunc(date2.getMinutes());
      var dateStr =
        year + "-" + mon + "-" + day + " " + noon + " " + hour + ":" + min;
      return dateStr;
    }
  }
};
</script>
<style scoped>
.appbackground {
  /* background: url('./assets/bck.jpg'); */
}
.content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
</style>
