<template>
  <div class="home-top-info">
    <div class="home-top-text">
      Previous winning information
      <br>Lucky number:
      <span class="text-wininfo">{{lastLucyNum}}</span>
      <br>Award amount:
      <span class="text-wininfo">{{awardAmt}} ETH</span>
      <br>Winner:
      <span class="text-wininfo">{{winner}}</span>
      <br>
    </div>
    <v-btn color="success" @click="betting">Play with 0.1 ETH</v-btn>
    <br>
    <br>
    <div class="rlink">
      My referral link:
      <span class="rlink-text">{{relink}}</span>
      <img class="copyicon" :data-clipboard-text="relink" src="../assets/copy.png">
    </div>
    <span>Copy green url text above and recommend to enjoy permanent income!</span>
    <!-- <v-btn color="info" @click="generatereferrallink">Generate my referral link</v-btn> -->
    <BasicTable :bettingInfo="bettingInfo" class="bettinginfo"/>
    <div class="faq">
      <span class="faq-title">What is ETH Lottery Mining?</span>
      <br>
      <span class="faq-text">
        It is a lottery mining game,You only need 0.1 ETH to play once.
        The lottery automatically announce when it reaches more than 20 people per round.Then a lucky number between 0 and 19 will be generated automatically(Nobody can calculate this number in advance).
        If you are this lucky number, you can get 0.8 ETH. More miraculous is if you recommend your referral link(the green url on the top) to others, you can enjoy his betting income forever.
      </span>
      <br>
      <br>
      <span class="faq-title">How to calculate the recommended income?</span>
      <br>
      <span class="faq-text">
        For example: A recommended his referral link to B. Every time B play the game, A will get 0.025 ETH(0.1*0.5*0.5), so if B play 10 times ,A will get 0.25 ETH,
        and B play 100 times ,A will get 2.5 ETH And so on. And if B recommended his referral link to C. in the same way,Every time C play the game ,B will get 0.025 ETH(0.1*0.5*0.5),in addition,
        A will still get 0.015 ETH(0.1*0.5*0.3). There are four levels of referral rewards.Note: one person only can use one referral link, so you should recommend to different people.
      </span>
      <br>
      <br>
      <span class="faq-title">When will i get my rewards?</span>
      <br>
      <span class="faq-text">
        You need to do notthing, Every time your referee played,the ETH will automatically transferred to you by the contract. Evert time? Yes!
        that's so cool! You can go to bed and ETH still be transferred to you!
      </span>
      <br>
      <br>
    </div>
    <Dialog :dialog="showDialog" :dialogMsg="dialogMsg"/>
  </div>
</template>
<script>
/* eslint-disable */
import BasicTable from "./BasicTable.vue";
import Dialog from "./Dialog";
import utils from "./utils.vue";
import { Base64 } from "js-base64";
import Clipboard from "clipboard";

export default {
  components: {
    BasicTable,
    Dialog
  },
  props: ["lastLucyNum", "awardAmt", "winner", "bettingInfo", "relink"],
  data() {
    return {
      showDialog: false,
      dialogMsg:
        "You are using android phone, please open MetaMask in the explorer menu to confirm the transaction!",
      isAndroid: false
    };
  },
  mounted() {
    var _this = this;
    var u = navigator.userAgent;
    this.isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1;
    // this.isWX=u.indexOf('MicroMessenger') > -1
    // this.isQQ= u.match('/\sQQ/i') === " qq"

    const clipboard = new Clipboard(".copyicon");
    clipboard.on("success", function(e) {
      _this.$toasted.show("copyed", {
        theme: "bubble",
        position: "top-center",
        duration: 3000
      });
    });
    clipboard.on("error", function(e) {});
  },
  methods: {
    betting() {
      var _this = this;
      var emptyAddr = "0x0000000000000000000000000000000000000000";

      //查询有没有推荐人
      this.myGlobal.contract.methods
        .xRecommendMap(this.myGlobal.userAccount)
        .call()
        .then(function(result) {
          var enb64addr = "";
          if (document.referrer.indexOf("ethlotterymining.com") > 0) {
            enb64addr = document.referrer.replace(
              "http://www.ethlotterymining.com/",
              ""
            );
          }

          if (result === emptyAddr && enb64addr !== "") {
            var recommender = enb64addr; //utils.decrypt(Base64.decode(enb64addr));

            if (recommender !== _this.myGlobal.userAccount) {
              console.log(recommender);
              _this.recommendAndPlay(recommender);
            } else {
              _this.gamePlay();
            }
          } else {
            _this.gamePlay();
          }
        });
    },
    recommendAndPlay(recommender) {
      this.myGlobal.contract.methods
        .recommendAndPlay(recommender)
        .send({
          from: this.myGlobal.userAccount,
          value: web3.toWei("0.1", "ether")
        })
        .then(function(result) {});

      if (this.isAndroid) {
        this.showDialog = true;
      }
    },
    gamePlay() {
      this.myGlobal.contract.methods
        .gamePlay()
        .send({
          from: this.myGlobal.userAccount,
          value: web3.toWei("0.1", "ether")
        })
        .then(function(result) {});
      if (this.isAndroid) {
        this.showDialog = true;
      }
    },
    generatereferrallink() {}
  }
};
</script>
<style scoped>
.home-top-info {
  margin-top: 30px;
  text-align: center;
}
.home-top-text {
  font-size: 20px;
  word-break: break-all;
}
.text-wininfo {
  color: red;
}
.bettinginfo {
  text-align: center;
  width: 80%;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
}
.rlink {
  word-break: break-all;
}
.rlink-text {
  color: green;
}
.faq {
  text-align: center;
  word-break: break-all;
  width: 80%;
  margin: 0 auto;
}
.faq-title {
  font-size: 25px;
}
.faq-text {
  font-size: 17px;
}
.emphasize-tx {
  color: green;
}
.copyicon {
  width: 15px;
  height: 15px;
  margin-left: 3px;
}
</style>
